
import { reactive, watch, ref } from "vue";

import { useStore } from "vuex";

import { MusicData, MovieData } from "@/types/musicApi";

import Drawer from "./component/drawer.vue";

import Control from "./component/control.vue";
export default {
  name: "music",
  setup(): unknown {
    // 音乐列表数据
    let list_startMore = reactive<MusicData[]>([]);
    let list_endMore = reactive<MusicData[]>([]);

    let drawer = ref<boolean>(false);
    let musicItem = reactive<MusicData>({
      name: "",
      singer: "",
      id: 0,
      imageUrl: "",
      playUrl: "",
    });

    // 剧
    let moList: MovieData[] = [
      {
        name: "三国演义",
        url: require("@/assets/img/likeMo1.webp"),
        introduce:
          "经典的四大名著之一, 完整地看了一遍。老三国是艺术品，百看不厌",
      },
      {
        name: "黑洞频率",
        url: require("@/assets/img/movie3.webp"),
        introduce:
          "与其说是一部科幻片，还不如说是一部亲情片,科幻因素只是锦上添花，打动我的还是其中的亲情和责任感。",
      },
      {
        name: "星际穿越",
        url: require("@/assets/img/movie7.webp"),
        introduce:
          "从头到尾除了震撼带给我的感动以外，其实还有亲情的伟大之处与人类的伟大之处给予的更多思考,不放弃是我们最好的解药，不要温和的走进那个良夜。",
      },
      {
        name: "雾山五行",
        url: require("@/assets/img/likeMov2.webp"),
        introduce:
          "非常喜欢这个背景水墨画般，体现浓郁的国风，这就是中国特色。故事情节人物设定打斗效果全都很棒",
      },
      {
        name: "钢琴家",
        url: require("@/assets/img/movie4.webp"),
        introduce:
          "很真实，战争带来了什么，他走在废墟那里，真的好伤感。还有无辜老人被丢下楼，好无助的感觉",
      },
      {
        name: "让子弹飞",
        url: require("@/assets/img/movie5.webp"),
        introduce:
          "神作，高能剧情，金句连连，演员演技没有一个拉胯的，尤其周润发、葛优、姜文的对手戏看的太过瘾了",
      },
      {
        name: "流浪地球",
        url: require("@/assets/img/movie8.webp"),
        introduce:
          "当初看这个片的时候真的被震撼到了，这对于中国科幻是一个里程碑的存在，希望能有越来越多的流浪地球吧！",
      },
    ];

    // 获取vuex
    const store = useStore();

    function getMusicIdEmit(id: number) {
      console.log(store.state.playStatus);
      console.log("#");

      if (store.state.playStatus.playId == id && store.state.playStatus.state) {
        console.log("@");
      } else {
        store.commit("updatePauseStatus", false);

        if (id >= 8) {
          list_endMore.forEach((item) => {
            if (item.id == id) {
              musicItem.id = item.id;
              musicItem.name = item.name;
              musicItem.imageUrl = item.imageUrl;
              musicItem.playUrl = item.playUrl;
              musicItem.singer = item.singer;
            }
          });
        } else {
          list_startMore.forEach((item) => {
            if (item.id == id) {
              musicItem.id = item.id;
              musicItem.name = item.name;
              musicItem.imageUrl = item.imageUrl;
              musicItem.playUrl = item.playUrl;
              musicItem.singer = item.singer;
            }
          });
        }
      }

      drawer.value = true;
    }

    watch(
      () => store.state.musicInfoLists,
      (newV) => {
        list_endMore = newV.slice(8);
        list_startMore = newV.slice(0, 8);
      },
      {
        immediate: true,
      }
    );

    return {
      moList,
      musicItem,
      list_startMore,
      list_endMore,
      drawer,
      getMusicIdEmit,
    };
  },
  components: {
    Drawer,
    Control,
  },
};
