
import { watch } from "vue";
export default {
  name: "drawer",
  props: {
    musicData: {
      type: Object,
      require: true,
    },
  },

  setup(props: any, context: any) {
    let musicInfo = { imageUrl: "" };

    function openDrawer(id: number) {
      // 触发自定事件
      context.emit("emit_id", id);
    }

    // 监视来自父组件的变化
    watch(
      () => props.musicData,
      (newVal) => {
        musicInfo = { ...newVal };

        musicInfo.imageUrl = require("@/assets/music/imageUrl/" +
          newVal.imageUrl.replace("@/assets/music/imageUrl/", ""));

      },
      { immediate: true }
    );

    return { musicInfo, openDrawer };
  },
};
